import React from "react";
class RentMotor extends React.Component {
  back = (e) => {
    e.preventDefault();
    this.props.prevStep();
  };
  handleForm = () => {
    this.props.handleAddNewUser();
  };
  render() {
    //re-render
    return (
      <div className="form">
        <div className="container">
          <div className="container-up motor">
            <div className="left" onClick={this.back}>
              {" "}
              THUÊ XE DU LỊCH
            </div>
            <div className="right"> THUÊ XE MÁY</div>
          </div>
          <div className="container-down">
            <div className="text">Đăng kí dịch vụ thuê xe máy</div>
            <div className="element-container">
              <div className="element-content ">
                <div className="left">
                  <div className="txt">
                    <label>Chọn loại xe</label>
                  </div>
                  <div>
                    <select
                      className="chonxe"
                      required="required"
                      aria-required="true"
                    >
                      <option>Xe số</option>
                      <option>Xe tay ga</option>
                      <option>Xe côn tay</option>
                    </select>
                  </div>
                </div>
                <div className="right">
                  <div className="txt">
                    <label>Số lượng xe</label>
                  </div>
                  <div>
                    <input type="number" placeholder="3" />
                  </div>
                </div>
              </div>
              <div className="element-content ">
                <div className="left">
                  <div className="txt">
                    <label>Thành phố</label>
                  </div>
                  <div>
                    <select
                      name="form_fields[thanh_pho]"
                      id="form-field-thanh_pho"
                      class="elementor-field-textual elementor-size-sm"
                      required="required"
                      aria-required="true"
                    >
                      <option value="Hà Giang">Hà Giang</option>
                      <option value="Sapa">Sapa</option>
                      <option value="Đà Lạt">Đà Lạt</option>
                      <option value="Phan Thiết">Phan Thiết</option>
                      <option value="Vũng Tàu">Vũng Tàu</option>
                      <option value="Mũi Né">Mũi Né</option>
                      <option value="Đà Nẵng">Đà Nẵng</option>
                      <option value="Huế">Huế</option>
                      <option value="Sài Gòn">Sài Gòn</option>
                      <option value="Hà Nội">Hà Nội</option>
                    </select>
                  </div>
                </div>
                <div className="right">
                  <div className="txt">
                    <label>Điểm nhận xe</label>
                  </div>
                  <div>
                    <input type="text" placeholder="Vd: Bến xe Hà Giang" />
                  </div>
                </div>
              </div>
              <div className="element-content ">
                <div className="left">
                  <div className="txt">
                    <label>Ngày nhận xe</label>
                  </div>
                  <div>
                    <input type="date" />
                  </div>
                </div>
                <div className="right">
                  <div className="txt">
                    <label>Giờ nhận xe</label>
                  </div>
                  <div>
                    <select
                      name="form_fields[gio_di]"
                      id="form-field-gio_di"
                      class="elementor-field-textual elementor-size-sm"
                      required="required"
                      aria-required="true"
                    >
                      <option value="6:00">6:00</option>
                      <option value="6:30">6:30</option>
                      <option value="7:00" selected="selected">
                        7:00
                      </option>
                      <option value="7:30">7:30</option>
                      <option value="8:00">8:00</option>
                      <option value="8:30">8:30</option>
                      <option value="9:00">9:00</option>
                      <option value="9:30">9:30</option>
                      <option value="10:00">10:00</option>
                      <option value="10:30">10:30</option>
                      <option value="11:00">11:00</option>
                      <option value="11:30">11:30</option>
                      <option value="12:00">12:00</option>
                      <option value="12:30">12:30</option>
                      <option value="13:00">13:00</option>
                      <option value="13:30">13:30</option>
                      <option value="14:00">14:00</option>
                      <option value="14:30">14:30</option>
                      <option value="15:00">15:00</option>
                      <option value="15:30">15:30</option>
                      <option value="16:00">16:00</option>
                      <option value="16:30">16:30</option>
                      <option value="17:00">17:00</option>
                      <option value="17:30">17:30</option>
                      <option value="18:00">18:00</option>
                      <option value="18:30">18:30</option>
                      <option value="19:00">19:00</option>
                      <option value="19:30">19:30</option>
                      <option value="20:00">20:00</option>
                    </select>
                  </div>
                </div>
              </div>
              <div className="element-content ">
                <div className="left">
                  <div className="txt">
                    <label>Ngày trả xe</label>
                  </div>
                  <div>
                    <input type="date" />
                  </div>
                </div>
                <div className="right">
                  <div className="txt">
                    <label>Giờ trả xe</label>
                  </div>
                  <div>
                    <select
                      name="form_fields[gio_di]"
                      id="form-field-gio_di"
                      class="elementor-field-textual elementor-size-sm"
                      required="required"
                      aria-required="true"
                    >
                      <option value="6:00">6:00</option>
                      <option value="6:30">6:30</option>
                      <option value="7:00" selected="selected">
                        7:00
                      </option>
                      <option value="7:30">7:30</option>
                      <option value="8:00">8:00</option>
                      <option value="8:30">8:30</option>
                      <option value="9:00">9:00</option>
                      <option value="9:30">9:30</option>
                      <option value="10:00">10:00</option>
                      <option value="10:30">10:30</option>
                      <option value="11:00">11:00</option>
                      <option value="11:30">11:30</option>
                      <option value="12:00">12:00</option>
                      <option value="12:30">12:30</option>
                      <option value="13:00">13:00</option>
                      <option value="13:30">13:30</option>
                      <option value="14:00">14:00</option>
                      <option value="14:30">14:30</option>
                      <option value="15:00">15:00</option>
                      <option value="15:30">15:30</option>
                      <option value="16:00">16:00</option>
                      <option value="16:30">16:30</option>
                      <option value="17:00">17:00</option>
                      <option value="17:30">17:30</option>
                      <option value="18:00">18:00</option>
                      <option value="18:30">18:30</option>
                      <option value="19:00">19:00</option>
                      <option value="19:30">19:30</option>
                      <option value="20:00">20:00</option>
                    </select>
                  </div>
                </div>
              </div>
              <div className="element-content ">
                <div className="left1">
                  <input className="a" type="checkbox" />
                  <label>Trả tại điểm nhận xe</label>
                </div>
              </div>
              <div className="btn-next" onClick={() => this.handleForm()}>
                Tiếp theo
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default RentMotor;
